@use 'sass:math';
@import '~scss/variables';
@import '~scss/custom-properties';

rc-sidebar {
  display: block;
  position: sticky;
  left: 0;
  width: $rc-sidebar-width-xs;
  background-color: #fff;

  @media (min-width: $screen-sm) {
    width: $rc-sidebar-width;
  }
}

// Sidebar container-scroller
.rc-sidebar-scroller {
  position: relative;
  height: 100%;
}

// Inner scrollable container
.rc-sidebar-scrollable {
  position: absolute;
  width: 100%;
}

// Row group header (Reservation Category)
.rc-row-group-header {
  display: flex;
  position: absolute;
  left: 0;
  width: 100%;
  height: $rc-row-group-height;
  padding: 0 5px;
  font-family: var(--font-family-minor);
  font-size: 11px;
  font-weight: 400;
  line-height: $rc-row-group-height;
  background-color: $rc-row-group-header-bg-color;
  color: $rc-row-group-header-color;
  text-transform: uppercase;

  & > span {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  @media (min-width: $screen-sm) {
    padding: 0 10px;
  }
}

// Row header (Reservation Unit)
.rc-row-header {
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  width: 100%;
  height: $rc-row-height;
  padding-left: 5px;
  line-height: 1;
  font-size: 12px;

  .rc-unit-name {
    max-height: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  @media (min-width: $screen-sm) {
    font-size: 1rem;
    //line-height: $rc-row-height;
    //white-space: nowrap;
    padding-left: 10px;
  }

  .rc-unit-icon {
    width: $rc-row-height;
    min-width: $rc-row-height;
    height: $rc-row-height;
    margin-left: auto;

    ff-img {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

// Horizontal grid
rc-sidebar rc-layer-grid-h {
  margin: 0 !important;
  width: 100%;
  height: 100%;
}

$rc-tooltip-width: 400px;

// Reservation Category tooltip
rc-sidebar .ff-tip {
  left: 10px;
  max-width: calc(100vw - 15px);
  width: $rc-tooltip-width;
  padding: .5rem .75rem .75rem .75rem;
  line-height: 1.25;

  @media (min-width: $rc-tooltip-width + math.div($rc-sidebar-width, 2)) {
    left: 50%;
  }
  @media (min-width: $rc-tooltip-width + $rc-sidebar-width) {
    left: 98%;
  }

  h6 {
    margin-bottom: .333rem;
    font-size: 1rem;
    font-family: var(--font-family-major);
    font-weight: bold;
  }

  .rc-tip-scroller {
    overflow-y: auto;
  }

  .rc-cat-descr {
    margin-top: .333rem;
    font-family: var(--font-family-major);
  }

  ff-header {
    padding-bottom: .25rem;
  }

  .rc-cat-pricing {
    margin-top: .5rem;
    font-family: var(--font-family-major);
    line-height: 1.35;

    .rc-cat-product-name, .rc-cat-item-price, .ff-header {
      font-weight: bold;
    }
  }

  ff-img {
    display: block;
    position: relative;
    overflow: hidden;
    padding-top: 60%;

    &.ff-img-error {
      display: none;
    }

    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
}
