@import '~scss/variables';
@import '~scss/custom-properties';

rc-calendar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  //margin-top: $header-banner-bar-size;

  //font-weight: 300;
  //text-transform: uppercase;

  ff-select {
    .ff-select.ff-select {
      padding-right: 20px;
      //text-transform: uppercase;
    }
  }
}

// Scroller container
.rc-scroller {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: auto;

  &::before { // scrollable margin for black header bar
    content: '';
    display: block;
    width: 100%;
    height: $header-banner-bar-size;
  }
}

// Reservation calendar layer
.rc-layer {
  display: block;
  position: absolute;
  margin-left: $rc-sidebar-width-xs;

  @media (min-width: $screen-sm) {
    margin-left: $rc-sidebar-width;
  }

  & > * {
    position: absolute;
  }
}

// Reservation calendar background layer (unavailable space)
rc-layer-background {
  background-color: $rc-unavailable-color;
}
