@import '~scss/variables';
@import '~scss/custom-properties';

rc-layer-grid-h, rc-layer-grid-v, rc-layer-grid-b {
  pointer-events: none;
}

// Horizontal borders
.rc-borders-h {
  width: 100%;
  border-top: 1px solid $rc-border-dark-color;
  border-bottom: 1px solid $rc-border-dark-color;
}

// Vertical borders
.rc-borders-v {
  width: 0;
  height: 100%;
  border-right: 1px solid $rc-border-light-color;
}

// Vertical bound borders
.rc-borders-b {
  width: 0;
  height: 100%;
  border-right: 1px solid var(--brand-color);
}
