@import '~scss/variables';
@import '~scss/custom-properties';

rc-availability {
  background-color: $rc-available-color;
  cursor: crosshair;

  rc-layer-active & {
    background-color: transparent;
    border: 1px solid transparent;
    transition: border-color .3s;

    &:hover {
      border-color: $text-color;
    }
  }
}
