@import '~scss/variables';
@import '~scss/custom-properties';

rc-header {
  display: block;
  position: sticky;
  top: 0;
  transform: translateZ(0);
  z-index: 1;
  font-family: var(--font-family-minor);
  text-transform: uppercase;

  .mode-day & {
    height: $rc-header-height-day;
  }
  .mode-week & {
    height: $rc-header-height-week;
  }

  @media #{$rc-mq-sticky} {
    .mode-day & {
      top: $rc-toolbar-height-day;
    }
    .mode-week & {
      top: $rc-toolbar-height-week;
    }
  }
}

// Scroller container
.rc-header-scroller {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

// Inner scrollable container
.rc-header-scrollable {
  position: absolute;
  height: 100%;
  background-color: #fff;
  margin-left: $rc-sidebar-width-xs;

  @media (min-width: $screen-sm) {
    margin-left: $rc-sidebar-width;
  }

  & > * {
    position: absolute;
  }
}

// Horizontal line
rc-header .rc-borders-h {
  border-top: 0;
  bottom: 5px;
}

// Vertical marks
rc-header .rc-borders-v {
  bottom: 0;
  height: 10px;
  border-color: var(--brand-color);
}

// Time markers
.rc-time {
  .mode-day & {
    transform: translateX(-33%);

    &.rc-highlight {
      span:first-child {
        position: relative;
        z-index: 0;
        color: var(--text-on-brand-color);

        &::before {
          content: '';
          display: block;
          position: absolute;
          top: -1px;
          bottom: -1px;
          left: -1px;
          right: -1px;
          z-index: -1;
          border-radius: 2px;
          background-color: var(--brand-color);
        }
      }
    }
  }
  .mode-week & {
    display: block;
    width: 0.5952381%;
    bottom: 10px;
    line-height: 1.3;
    z-index: 0;
    text-align: center;
    font-size: 10px;
    color: #fff;

    // Green plate
    &.rc-highlight::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: -1px;
      bottom: 0;
      z-index: -1;
      background-color: $extra-bg-color;
    }
  }

  // AM/PM marker
  sup {
    left: 2px;
  }
}

// Weekday
.rc-weekday {
  padding: 5px;
  font-size: 18px;
  line-height: 1.15;
  text-align: center;
  color: var(--text-on-brand-color);
  background-color: var(--brand-color);
  border-top: 1px solid var(--brand-color);
  border-bottom: 1px solid var(--brand-color);
}

// Small calendar pictogram
rc-header .rc-pictogram {
  position: absolute;
  top: 0;
  right: 3px;
  min-width: 24px;
  border-width: 2px;
  border-color: var(--text-on-brand-color);
  font-size: 16px;

  .serifs {
    top: -7px;
    hr {
      background: var(--text-on-brand-color);
      flex: 0 0 2px;
      height: 6px;
    }
  }
}

// Boundary grid
rc-header rc-layer-grid-b {
  margin: 0 !important;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
