@import '~scss/variables';
@import '~scss/custom-properties';

rc-adjustments {
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  left: 0;
  width: $rc-sidebar-width-xs;
  padding: 3px 4px 0 3px;
  background-color: #fff;
  z-index: 1;
  font-family: var(--font-family-minor);
  font-size: .7rem;
  text-transform: uppercase;

  @media #{$rc-mq-sticky} {
    .mode-day & {
      top: $rc-toolbar-height-day;
    }
    .mode-week & {
      top: $rc-toolbar-height-week;
    }
  }

  .mode-day & {
    width: $rc-sidebar-width-xs - 24px;
    height: $rc-header-height-day;
    margin-top: -$rc-header-height-day;
  }
  .mode-week & {
    height: $rc-header-height-week;
    margin-top: -$rc-header-height-week;;
    padding: 10px 32px 0 3px;
  }

  @media (min-width: $screen-sm) {
    width: $rc-sidebar-width;
    .mode-day & {
      width: $rc-sidebar-width - 24px;
    }
  }

  // Zoom label
  label {
    display: flex;

    & > span:first-child {
      margin-right: 5px;
    }
  }
}

// Calendar range slider
.rc-range {
  margin: 0;
  padding: 0;
}
