@import '~scss/variables';
@import '~scss/custom-properties';

@import 'rc-legend';
@import 'rc-options';

rc-toolbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  position: sticky;
  left: 0;
  padding: 0 10px;
  z-index: 10;
  background-color: #fff;

  //@media (min-width: $collapse-sidebar-width) {
  //  padding: 0 10px;
  //}

  @media (min-width: 580px) {
    flex-wrap: nowrap;
    justify-content: space-between;

    .mode-day & {
      height: $rc-toolbar-height-day;
    }
    .mode-week & {
      height: $rc-toolbar-height-week;
    }
  }

  @media #{$rc-mq-sticky} {
    top: 0;
  }

  rc-date-selector {
    margin-top: 5px;
  }
}
